$(function () {
  function getstaffs() {
    $.ajax({
      url: 'http://test.oms.hwariot.com/api/v1/news/?page_size=4',
      type: 'GET',
      dataType: "json",
      data: {},
    }).done(res => {
      console.log(res)
      if (res.count > 0) {
        var page ='';
        res.results.forEach((v,i)=>{
          var a = v.summary.length > 40 ? "..." : "";
          page +=`
          <li>
            <a href="/@/staffdetail.html?id=${v.id}">
              <div class="imgbox">
                <img src="${v.image_url}" alt="">
              </div>
              <div class="text">
                <h3>${v.title}</h3>
                <p>${v.summary.substr(0, 40)+a}</p>
                <span>${v.created_time}</span>
              </div>
            </a>
          </li>`
        })
        $('#staff-list').html(page)
      }
    })
  }
  getstaffs()
})